
import { Core } from '.';
import { Site } from '../Project/Site';
import { getFalseColor } from 'njscore';

export class Scene {
    public core: Core;
    public scene: Scene;
    public colorArray1: number[][] = [];
    public colorArray2: number[][] = [];

    private _capture: Site | undefined;

    constructor(core: Core) {
        this.core = core;
    }
    public init() {
       this.colorArray1 = [];
        for (let i = 0; i < 255; ++i) {
            const result = getFalseColor(i / 255);
            this.colorArray1.push(result);
        }

        this.colorArray2 = [];
    }
    public render(ctx: CanvasRenderingContext2D) {

        const xOff = ctx.canvas.width - 300;
        const yOff = ctx.canvas.height - 50;

        const w = 1;
        const h = 10;
        for (let i = 0; i < this.colorArray1.length; ++i) {
            ctx.beginPath();
            ctx.rect(i * w + xOff, yOff, w, h);
            ctx.closePath();
            ctx.fillStyle = `rgba(${this.colorArray1[i][0]}, ${this.colorArray1[i][1]}, ${this.colorArray1[i][2]}, 1)`;
            ctx.fill();
        }

        // if (this._capture !== undefined) {
        //     this._capture.render(ctx);
        // } else {
        this.core.app.project.sites.forEach((site) => {
            site.renderDot(ctx);
        });
        // }

        if (this._capture !== undefined) {
            this._capture.render(ctx);
        }

        this.core.app.addon.render(ctx);

        ctx.beginPath();
        ctx.font = "14px Arial";
        ctx.fillStyle = '#fff';
        const log = `latitude: ${this.core.activeCommand.m[3].toFixed(4)}, longitude: ${this.core.activeCommand.m[2].toFixed(4)}`;
        ctx.fillText(log, ctx.canvas.width * 0.5 - 100, 50);

        if (this.capture) {
            const logSite = this.capture.getLog();
            ctx.fillText(logSite, ctx.canvas.width * 0.5 - 100, 80);
        }
    }
    public set capture(v: Site | undefined) {
        // console.trace();
        if (this._capture !== undefined) {
            this._capture.isActivated = false;
            this._capture.blur();
        }

        this._capture = v;
        if (this._capture !== undefined) {
            this._capture.isActivated = true;
            this._capture.focus();
        } 
        // console.log(this._capture)

    }
    public get capture(): Site | undefined {
        return this._capture;
    }
}