import { getCSVFromURL } from 'njscore';

export class WaterQualityInspector {
    data = {};

    day = 24;
    scale = 1.0
    offsetData = 4;
    constructor() {

        document.onkeydown = (k) => {
            this.offsetData++;
            console.log(this.offsetData)
        }

        getCSVFromURL(process.env.PUBLIC_URL + '/static/data/Watabot/obs_104_1928_d047_15c7.csv').then((d) => {
            const lines = d.split(/\r\n|\r|\n/g);
            const index = lines[0].split(',');

            const dataDic = {}
            let needComputeData = true;
            for (let i = 0; i < index.length; ++i) {
                console.log(index[i])
                if (needComputeData && index[i] === 'time (UTC)') {
                    dataDic[index[i]] = [];
                    needComputeData = false;
                } else {
                    dataDic[index[i]] = new Float32Array(lines.length - 1)
                }
            }

            for (let i = 1; i < lines.length; ++i) {
                const row = lines[i].split(',');
                for (let j = 0; j < index.length; ++j) {
                    const theIndex = index.indexOf(index[j]);
                    if (theIndex !== -1) {
                        dataDic[index[j]][i - 1] = row[theIndex]
                    }
                }

            }
            this.data = dataDic;
            console.log(dataDic)
        });
    }
    public Offset(v) {
        if (v < 0) {
            // this.offsetData -= this.offsetData * 0.5;
        } else {
            // this.offsetData += this.offsetData * 0.5;
        }
    }
    public wheel(e: WheelEvent) {
        if (e.deltaY < 0) {
            this.scale += this.scale * .25;
        } else {
            this.scale -= this.scale * .25;
        }
        this.scale = this.scale < 0.012 ? 0.012 : this.scale;
        console.log(this.scale)
    }

    public render(ctx: CanvasRenderingContext2D) {
        let d = this.data['sea_water_temperature (K)']; // sea_water_temperature (K) sea_water_turbidity (1)

        dd(ctx, d, this.scale, this.day)

        vizData(ctx, d, '#f00', this.scale, this.day, this.offsetData)

        d = this.data['sea_water_turbidity (1)'];
        vizData(ctx, d, '#0f0', this.scale, this.day, this.offsetData)

        d = this.data['chlorophyll_fluorescence (RFU)'];
        vizData(ctx, d, '#00f', this.scale, this.day, this.offsetData)

        d = this.data['oxidation_reduction_potential (mV)'];
        vizData(ctx, d, '#0ff', this.scale, this.day, this.offsetData)

    }
}

const dd = (ctx: CanvasRenderingContext2D, d, scale, day) => {
    const hourRes = 12

    for (let i = 0; i < d.length; i++) {

        ctx.lineWidth = 0.5;
        ctx.strokeStyle = `rgba(200, 200, 200, 1)`;
        if (scale > 0.1) {
            if (i % (hourRes * day) === 0) {
                const x = i * scale;
                ctx.beginPath();
                ctx.moveTo(x, 0);
                ctx.lineTo(x, ctx.canvas.height);
                ctx.stroke();
            }
        }

        ctx.lineWidth = 1;
        ctx.strokeStyle = `rgba(0, 0, 255, 1)`;
        if (scale > 0.02) {
            if (i % (hourRes * day * 7) === 0) {
                const x = i * scale;
                ctx.beginPath();
                ctx.moveTo(x, 0);
                ctx.lineTo(x, ctx.canvas.height);
                ctx.stroke();
                // console.log(i)
            }
        }
        ctx.lineWidth = 1;
        ctx.strokeStyle = `rgba(0, 255, 0, 1)`;
        if (i % (hourRes * day * 30) === 0) {
            const x = i * scale;
            ctx.beginPath();
            ctx.moveTo(x, 0);
            ctx.lineTo(x, ctx.canvas.height);
            ctx.stroke();
            // console.log(i)
        }
    }
};

const vizData = (ctx: CanvasRenderingContext2D, d, color, scale, day, offsetData) => {

    ctx.lineWidth = 3;
    ctx.strokeStyle = color;

    ctx.beginPath();
    let x = 0
    ctx.moveTo(x, d[0])
    for (let i = 1; i < d.length; i += offsetData) {
        x += 1

        const xP = x * scale * offsetData
        let yP = 0;
        let val = 0
        let key = 'max' // max
        switch (key) {
            case 'mean':
                for (let j = 0; j < offsetData; ++j) {
                    val += d[i + j]
                }

                yP = val / offsetData;
                ctx.lineTo(xP, yP)
                break;
            default:
                for (let j = 0; j < offsetData; ++j) {
                    if (val < d[i + j]) {
                        val = d[i + j]
                    }
                }
                yP = val // / offsetData;
                ctx.lineTo(xP, yP)
                break;
        }

        // console.log(i)
    }
    // console.log(x)
    ctx.stroke();
};